























































































module.exports =
  mixins: [
    require("~plugins/get-by-mount") "24h", "sow/story", -> @book_id
    require '~plugins/book'
    require('~plugins/pager')
      watch: (val, key)->
        @$store.commit "menu/focus", @chat_id if @chat_id
  ]
  methods:
    focus: (@idx)->
    anker: (book_id, a)->
      @$router.push
        path: "../#{@part_id}/anker"
        query: { a, @back }

    part_prev: ->
      @book =
        part_id: @part_prev_id ? @part_id
        page_idxs: [0]

    part_next: ->
      @book =
        part_id: @part_next_id ? @part_id
        page_idxs: [0]

  computed:
    part_prev_id: ->
      if @part && @book
        ids = @book.parts.pluck('id')
        idx = ids.indexOf @part_id
        ids[idx - 1]

    part_next_id: ->
      if @part && @book
        ids = @book.parts.pluck('id')
        idx = ids.indexOf @part_id
        ids[idx + 1]
    
    page_all_contents: ->
      @chats(@part_id)




